        },
      });
    },
    clicbtn() {
      const menu = document.querySelector(".mobile-menu");

      menu.classList.toggle("hidden");
    },
  },
  computed: {
    ...mapGetters(["username"]),
  },
  created() {
    this.id = this.$route.params.id;
  },
};
</script>

