<template>
  <div class="flex flex-col w-full h-full bg-slate-100">
    <div class="lg:w-10/12 mx-auto mt-20 flex">
      <loda v-if="loading" class="m-auto" />

      <div class="w-full" v-else>
        <DoctorInfo
          :res="r"
          v-for="r in res"
          :key="r.id"
          class="box-content mt-5 border-y-5 border-white"
        />
      </div>

      <div
        class="p-6 m-6 md:flex filters rounded-lg hidden lg:block h-96 mb-44"
      >
        Filters {{ this.city }}
      </div>
    </div>
    <!-- <h1>
      doctors/?doctor__first_name=Mahmoud&doctor__last_name=&doctor__specialization__speciality=&doctor__qualifications=&clinic__location__name=&clinic__name=
    </h1> -->
  </div>
</template>

<script>
import loda from "@/components/SpinnerScreen.vue";
import DoctorInfo from "@/components/DoctorInfo.vue";
import axios from "axios";
export default {
  data: function () {
    return {
      city: "",
      area: "",
      name: "",
      sp: " ",
      res: [{ doctor: { clinic_number: "125", appointment_price: 20 } }],
      loading: false,
    };
  },
  props: ["re"],
  components: { DoctorInfo, loda },
  methods: {
    getready() {
      this.name = this.$route.params.name;

      (this.city = this.$route.params.city),
        (this.area = this.$route.params.area),
        (this.sp = this.$route.params.sp);
    },
    async getdata() {
      if (this.name == "doctor") {
        this.loading = true;
        const response = await axios.get(
          // `/doctors/?doctor__full_name=&doctor__area_or_center=${this.area}&doctor__specialization=${this.sp}&doctor__location=${this.city}&doctor__qualifications=`
          `/doctors/?doctor__full_name=&doctor__address__name=&doctor__address__location__city=&doctor__specialization__specialization=&doctor__qualifications=`
        );
        this.loading = false;
        this.res = response.data.results;
      } else {
        const response = await axios
          .get
          // `/doctors/?doctor__full_name=&doctor__area_or_center=&doctor__specialization=${this.sp}&doctor__location=&doctor__qualifications=`

          // `/doctors/?doctor__first_name=${this.name}&doctor__last_name=&doctor__specialization__speciality=&doctor__qualifications=&clinic__location__name=${this.city}&clinic__name=`
          ();
        this.res = response.data.results;
      }

      console.log("res", this.res);
    },
    async getuser() {
      const response = await axios.get(
        `/patients/profiles/${this.$route.params.id}/`,
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      this.res = response.data;
      this.$store.dispatch("username", response.data.patient.name);

      console.log(response);
    },
  },
  created() {
    // this.getuser();
    this.getready();
    this.getdata();
  },
};
</script>

