<template>
  <div
    class="w-full min-h-screen m-auto bg-slate-100 flex items-center justify-center"
  >
    <div class="container1">
      <div class="main sign-in">
        <div class="card">
          <div class="logo"></div>
          <div class="card-head">
            <h3 class="header text-teal-700">دخول</h3>
          </div>
          <div
            v-if="error"
            class="p-4 mb-4 text-sm text-red-800 rounded-lg bg-red-50"
            role="alert"
          >
            <span class="font-medium">{{ error }}</span>
          </div>
          <div class="card-body">
            <form id="frmLogin" @submit.prevent="handalSubmit">
              <div class="form-group">
                <label class="form-label text-right"
                  >الموبايل او البريد الالكتروني</label
                >
                <input
                  v-model="email"
                  required="email"
                  class="form-control"
                  name="email"
                  id="txtUsername"
                  type="text"
                />
              </div>
              <div class="form-group">
                <label class="form-label text-right">كلمة المرور</label>
                <input
                  v-model="password"
                  class="form-control"
                  required="password"
                  name="password"
                  id="txtPassword"
                  type="password"
                />
              </div>
              <div class="form-group">
                <button
                  type="submit"
                  class="btn bg-teal-700 text-center pb-4"
                  id="btnSignIn"
                >
                  <span> دخول</span>
                </button>
              </div>
            </form>
          </div>
          <div class="card-footer">
            <span class="text-teal-700">
              <a href="#"> نسيت كلمة المرور؟</a></span
            >
            <span class="text-red-400"
              >مستخدم جديد ؟
              <a @click="RedirectToSignUp()" class="text-teal-700"
                >انضم الان</a
              ></span
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: function () {
    return {
      email: "",
      password: "",
      error: "",
    };
  },
  methods: {
    async handalSubmit() {
      try {
        const response = await axios.post("patients/login/", {
          email_or_phone: this.email,
          password: this.password,
        });
        console.log(response.data.access);
        localStorage.setItem("token", response.data.access);
        // localStorage.setItem("token", response.data.acc);

        // localStorage.setItem("token", response.data);

        // localStorage.setItem("username", response.data.username);

        // console.log("username", response.data.username);
        this.$store.dispatch("username", response.data.username);
        this.$router.push({
          name: "home",
          params: {
            id: response.data.profile_id,
          },
        });
      } catch (e) {
        this.error = "Invalid email or password !";
      }
    },
    RedirectToSignUp() {
      this.$router.push("signup");
    },
  },

  created() {
    // this.cont();
  },
  mounted() {
    // this.getf();
  },
};
</script>
