<template>
  <div>
    <div
      class="w-5/6 mx-auto wraperrr h-screen flex flex-row items-center justify-center mt-2"
    >
      <div v-if="!is_reserved" class="Reservation md:w-1/2">
        <h1>ادخــــل بـــيــــانـــات الــحــجــز</h1>
        <form action="">
          <div class="flex items-end flex-col">
            <label class="" for="">اسم المريض</label>
            <input
              v-model="patient_name"
              id=""
              type="text"
              class="block w-full px-4 py-2 mt-2 text-gray-700 bg-white border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none focus:ring"
            />
          </div>
          <div class="flex items-end flex-col">
            <label class="text-black text-right" for="">رقم الموبايل</label>
            <input
              v-model="patient_phone"
              id=""
              type="text"
              class="block w-full px-4 py-2 mt-2 text-gray-700 bg-white border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none focus:ring"
            />
          </div>
          <div class="flex items-end flex-col">
            <label class="text-black" for="">البريد الالكتروني(اختياري)</label>
            <input
              v-model="patient_email"
              id=""
              type="text"
              class="block w-full px-4 py-2 mt-2 text-gray-700 bg-white border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none focus:ring"
            />
          </div>
          <button
            @click="hadelSubmit()"
            type="button"
            class="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-xl px-5 mr-2 mt-5 mb-2 w-full py-2"
          >
            <h1>حجز</h1>
          </button>
        </form>
      </div>
      <div v-else class="Reservation md:w-1/2">
        <h1>تم الحجز بنجاح</h1>
        <button
          @click="$router.push('/')"
          type="button"
          class="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-xl px-5 mr-2 mt-5 mb-2 w-full py-2"
        >
          الرجوع الي الصفحة الرئسية
        </button>
      </div>
      <div class="flex flex-col items-end px-3 justify-center w-full h-1/2">
        <h1>الدكتور : {{ doc.doctor.full_name }}</h1>
        <h1>رقم الموبايل : {{ doc.doctor.clinic_number }}</h1>
        <h1>السعر : {{ doc.doctor.appointment_price }}</h1>
      </div>
    </div>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import axios from "axios";
export default {
  data: function () {
    return {
      doc: {},
      patient_name: "",
      patient_phone: "",
      patient_email: "",
      is_reserved: false,
    };
  },
  methods: {
    showAlert(message, icon) {
      Swal.fire({
        title: message,
        icon: icon,
        showConfirmButton: false,
        timer: 2000,
      });
    },
    getDoc() {
      axios
        .get(`/doctors/${this.$route.params.docId}`)
        .then((res) => {
          this.doc = res.data;
          console.log(this.doc);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    hadelSubmit() {
      axios
        .post(`/appointments/`, {
          doctor_profile_id: this.$route.params.docId,
          time_slot_id: this.$route.params.time,
          patient_phone_number: this.patient_phone,
          patient_email: this.patient_email,
          patient_full_name: this.patient_name,
        })
        .then((res) => {
          this.showAlert("تم الحجز بنجاح", "success");
          this.is_reserved = true;
          this.patient_email = "";
          this.patient_name = "";
          this.patient_phone = "";

          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getDoc();
  },
};
</script>
