import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    username: null,
  },
  getters: {
    username: (state) => {
      return state.username;
    },
  },
  mutations: {
    username(state, username) {
      state.username = username;
    },
  },
  actions: {
    username(context, username) {
      context.commit("username", username);
    },
  },
  modules: {},
});

