import Vue from "vue";
import VueRouter from "vue-router";

import Home from "../views/HomePage.vue";
import login from "../views/LoginPage.vue";
import reslut from "../views/SearchResult.vue";
import signup from "../views/SignUp.vue";
import secl from "../components/SelectExp.vue";
import meeting from "../components/MobileRservation.vue";
import info from "../components/DoctorInfo.vue";
import doctor from "../components/DocSign.vue";
import DocSign from "../components/SignUpDoc.vue";
import dashboard from "../views/DoctorDash.vue";
import CreateReservations from "../components/CreateReservation.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "homel",
    component: Home,
  },
  {
    path: "/home/:id",
    name: "home",
    component: Home,
  },
  {
    path: "/signup",
    name: "signup",
    component: signup,
  },
  {
    path: "/CreateReservations/:docId/:time/",
    name: "CreateReservations",
    component: CreateReservations,
  },
  {
    path: "/DocSignUP",
    name: "DocSign",
    component: DocSign,
  },
  {
    path: "/info",
    name: "info",
    component: info,
  },
  {
    path: "/meeting/:docId",
    name: "meeting",
    component: meeting,
  },
  {
    path: "/result",
    name: "result",
    component: reslut,
  },
  {
    path: "/doctor",
    name: "doctor",
    component: doctor,
  },
  {
    path: "/s",
    name: "s",
    component: secl,
  },

  {
    path: "/login",
    name: "login",
    component: login,
  },
  {
    path: "/reslut/:sp/:city/:area/:name",
    name: "reslut",
    component: reslut,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
  {
    path: "/dashboard/:id",
    name: "dashboard",
    component: dashboard,
    children: [
      {
        path: "/addtimeslots",
        name: "times",
        component: () =>
          import(
            /* webpackChunkName: "Overview" */ "../views/AddTimeSlots.vue"
          ),
      },
      {
        path: "/profile/:id",
        name: "profile",
        component: () =>
          import(
            /* webpackChunkName: "Overview" */ "../components/DocProfile.vue"
          ),
      },
      {
        path: "/yourtimeslots",
        name: "yourtimeslots",
        component: () =>
          import(/* webpackChunkName: "Overview" */ "../views/TimeSlot.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  mode: "hash",
  // base: process.env.BASE_URL,
  routes,
});

export default router;
