<script>
import SElectMeet from "./SElectMeet.vue";
import Swal from "sweetalert2";
import axios from "axios";
export default {
  data: function () {
    return {
      doc: {},
      patient_name: "",
      patient_phone: "",
      patient_email: "",
      is_reserved: false,
    };
  },
  components: { SElectMeet },
  methods: {
    showAlert(message, icon) {
      Swal.fire({
        title: message,
        icon: icon,
        showConfirmButton: false,
        timer: 2000,
      });
    },
    getDoc() {
      axios
        .get(`/doctors/${this.$route.params.docId}`)
        .then((res) => {
          this.doc = res.data;
          console.log(this.doc);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    hadelSubmit() {
      axios
        .post(`/appointments/`, {
          doctor_profile_id: this.$route.params.docId,
          time_slot_id: this.$route.params.time,
          patient_phone_number: this.patient_phone,
          patient_email: this.patient_email,
          patient_full_name: this.patient_name,
        })
        .then((res) => {
          this.showAlert("تم الحجز بنجاح", "success");
          this.is_reserved = true;
          this.patient_email = "";
          this.patient_name = "";
          this.patient_phone = "";

          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getDoc();
  },
};
</script>
